.MyTodoContainer{
    display: flex;
    flex-wrap: wrap;
    padding: 0 30px;    
    gap: 20px;
    justify-content: center;


}
.CardContainer{
    width: 100%;
    display: grid;
     grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    /* justify-content: center; */
    /* gap: 10px; */
}

@media screen and (max-width:480px) {
    .filter-container{
        flex-direction: column;
        gap: 10px;
    }
    .filter-container h4 {
        font-size: 22px;
        font-weight: 600;
    }
    .CardContainer{
        grid-template-columns: 1fr;
        margin: 0;
        gap: 0px;
    }
}