.form-floating {
    position: relative;
    margin-bottom: 1rem;
}

.form-label {
    font-size: 1rem;
    font-weight: 400;
    color: #535252;
}

.form-control {
    background: transparent;
    padding: 0.375rem 0.75rem;
    font-size: 1rem;
    line-height: 1.5;
    border-radius: 0.25rem;
    border: 1px solid #ced4da;


}

#floatingTextarea::-webkit-scrollbar {
    display: none;
}

.form-control:focus {
    border-color: #80bdff;
    box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, .25);
}

.form-control::placeholder {
    color: #6c757d;
    opacity: 1;
}

.form-floating label {
    width: max-content;
    height: max-content;
    background: white;
    position: absolute;
    top: 0.75rem;
    padding: 0 0.375rem;
    font-size: 1rem;
}