.form-container {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
}

.form {
    display: flex;
    flex-direction: column;
    align-items: center;
    border: 1px solid gray;
    border-radius: 5px;
    padding: 20px 0px;
    width: 400px;
    box-shadow: 0px 3px 1px gray;
}
.fa-circle-user{
    margin-bottom: 30px;
    font-size: 150px;
    display: flex;
    justify-content: center;
    color: #151515;
}

.login-btn{
    background-color: #151515;
    width: 100%;
    height: 50px;
    border: none;
    border-radius: 5px;
    color:#ffffff
}
.login-btn:hover{
    background-color: #2c2c2c;
}