@import url('https://fonts.googleapis.com/css2?family=Inter:ital,opsz,wght@0,14..32,100..900;1,14..32,100..900&family=Outfit:wght@100..900&display=swap');
.add-task{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    margin-top: 20px;

}

.add-task h1{
    font-family: 'Courier New', Courier, monospace;
    font-weight: bold;

}

.add-task input[type="search"]{
    width: 40%;
    height: 40px;
    border-radius: 5px;
    border: 1px solid #ccc;
    padding: 0 10px;
}
.add-task button{
 padding: 8px 10px;
 max-width: 300px;
 min-width: 150px;
}


/* ============= card Styles ============== */
.chead{
    display: flex;
    flex-direction:row;
    justify-content: space-between;

}
.chead .task-inc{
    background-color: #f7e373;
    padding: 5px 10px;
    font-family: cursive;
    border-radius: 5px;
}
.chead .task-cmp{
    background-color: #adfa49;
    padding: 5px 10px;
    font-family: cursive;
    border-radius: 5px;
}

/* ============= */
.filter-container{
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 20px 0px;
}
.filter-group{
    margin-left: 20px;
    border: none;
    padding: 10px;
    border-radius: 5px;
    /* background: #000; */
}

/* card.jsx */
.card {
    margin: 10px 0;
    width: 35%;
    min-width: 300px;
    height: 270px;
    border-radius: 22px;
    box-shadow: 1px 2px 6px rgb(0 0 0);
    overflow: auto;
    background: linear-gradient(160deg, #4fd1c5 0%, #3ba6f6 45%, #2457f5 100%);
    position: relative;
    
    border: none;
}

.card::-webkit-scrollbar {
    display: none;
}

.card-header {
    background:transparent;
    position: sticky;
    width: 100%;
    top: 0;
}
.chead{
    align-items: baseline;
    color: #fff;
}
.chead .task-inc {
    color: black;
}

.card-body {
    padding: 5px 20px;
    position: relative;
    height: 200px;
    overflow: auto;
    color: #fff;
}

.card-body::-webkit-scrollbar {
    display: none;
}

.card-body h6 {
    font-family: "Inter", sans-serif;
    position: relative;
    font-size: 1.1rem;
    font-weight: 500;
    letter-spacing: -0.5px;
}

.card-body p {
    font-family: "Inter", sans-serif;
    font-size: 0.8rem;
    color: #fff;
    margin-top: 5px;
    margin-bottom: 10px;
    line-height: 20px;

}

.card-body .DateFormate {
    position: absolute;
    bottom: 0px;
    right: 20px;
    margin: 0;
    background: transparent;
    z-index: 1055;
}
.DateFormate{
    padding-left: 20px;
    color: #fff;
}

.card-footer {
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: sticky;
    width: 100%;
    bottom: 0;
    background-color: transparent;
}

.card-footer button {
    width: 45%;
    height: auto;
    padding: 4px;
    margin: 4px;}

.btn-warning {
    background: #F2B635;
    color: #fff;
}
.btn-danger{
    background: #e64b3a;
    color: #fff;
}
.btn-danger:hover{
    background: #fff;
    color: #e64b3a;
}

@media screen and (max-width:480px) {
    .form{
        margin: 0 20px;
    }
    .card {
        width: 100%;
        min-width: 100%;
        height: auto;
    }
    .card-body {
        height: auto;
    }
    .card-footer {
        flex-direction: column;
    }
    .card-footer button {
        width: 100%;
        margin-bottom: 10px;
    }
    /*  */
    .add-task{
        flex-direction: column;
        padding-bottom: 10px;
    }
    .add-task h1{
        margin-bottom: 5px;
    }
    .add-task input[type="search"]{
        width: 80%;
        margin: 0 0;
    }
    .add-task button{
        width: 80%;
        max-width: none;
        min-width: none;
        margin-bottom: 0px
    }
}